:host {
  @include reset-host;
}

:host .author {
  display: flex;
  gap: rem(24);
  align-items: flex-start;
  padding: 64px 10%;
  background: $gray-100;

  @include mq('max', 'md') {
    flex-direction: column;
    padding: 50px rem(30);
  }
}

:host .info {
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  gap: rem(16);
  align-items: center;
  width: rem(240);
  padding: 28px 16px;

  border: 1px solid $gray-700;
  border-radius: rem(6);

  @include mq('max', 'md') {
    flex-direction: row;
    flex-wrap: nowrap;
    width: unset;
    padding: 16px;
  }
}

:host .author-image {
  position: relative;
  flex-shrink: 0;
  width: rem(64);
  height: rem(64);

  overflow: hidden;
  border-radius: 50%;

  .image {
    @include cover;
  }
}

:host .author-info {
  dt {
    margin-bottom: rem(4);
    @include font('head-4');
    color: $white;
  }

  @include mq('min', 'md') {
    text-align: center;
  }

  dd {
    @include font(body-small);
    color: $gray-800;
  }
}

:host .about {
  bs-title {
    display: block;
    margin-bottom: rem(8);
  }
}
