import { Component, Host, h, Prop } from '@stencil/core';
import { Memoize, parseIfString } from '@springbokagency/stencil-base';
import type { BsImage } from '../../atoms/image/image';
import { TitleColor, TitleSize, TitleTag } from '../../atoms/title/title.types';
import { TextColor } from '../../atoms/text/text.types';

/**
 * Author component.
 */
@Component({
  tag: 'bs-author',
  styleUrl: 'author.scss',
  shadow: true,
})
export class BsAuthor {
  /**
   * Description.
   */
  @Prop() authorImage: BsImage | string;

  @Memoize('authorImage')
  get parsedAuthorImage(): BsImage {
    return parseIfString(this.authorImage);
  }

  /**
   * Author's name
   */
  @Prop() authorName: string;

  /**
   * Author's job position
   */
  @Prop() authorPosition: string;

  /**
   * Main about title
   */
  @Prop() aboutTitle: string;

  /**
   * Description text
   */
  @Prop() description: string;

  render() {
    return (
      <Host>
        <div class="author">
          {/* Info */}
          <div class="info">
            {/* Image */}
            <div class="author-image">
              <bs-image class="image" {...this.parsedAuthorImage} cover />
            </div>

            {/* Author info */}
            <dl class="author-info">
              <dt>{this.authorName}</dt>
              <dd>{this.authorPosition}</dd>
            </dl>
          </div>

          {/* About */}
          <div class="about">
            <bs-title
              tag={TitleTag.H2}
              color={TitleColor.White}
              text={this.aboutTitle}
              size={TitleSize.Head4}
            />

            <bs-text textColor={TextColor.White} text={this.description} />
          </div>
        </div>
      </Host>
    );
  }
}
